/* Import fonts */
@import './fonts/fonts.css';

/* Base styles */
.App {
  text-align: center;
  padding: 20px;
  background-color: #FFFEFA;
  min-height: 100%;
}

/* Title screen */
.title {
  font-family: 'Punx Grotesk', system-ui, sans-serif;
  font-size: 1.5rem;
  line-height: 0.8;
  letter-spacing: -0.1rem;
  color: #000000;
  margin-bottom: 2rem;
}

/* Game elements */
.phrase {
  font-family: 'Bookman Old Style', serif;
  font-size: 9rem;
  line-height: 1;
  color: #000000;
  display: inline;
  text-align: left;
  padding-left: 40px;
}

/* UI elements */
.score-display, 
.back-button,
.punctuation-button {
  font-family: 'Rethink Sans', system-ui, sans-serif;
  font-weight: 800;
}

.character-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.character-card {
  background: white;
  padding: 30px;
  border-radius: 16px;
  cursor: pointer;
  transition: transform 0.2s;
  border: 4px solid;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.character-card:hover {
  transform: scale(1.05);
}

.character-card-image {
  height: 200px;
  width: auto;
  object-fit: contain;
}

.character-card h2 {
  margin: 0;
  color: #000000;
  font-family: 'Rethink Sans', system-ui, sans-serif;
  font-weight: 800;
}

/* Game Screen Styles */
.game-screen {
  position: relative;
  max-width: 100%;
  margin: 0 auto;
  padding: 20px;
  padding-top: 80px;
}

.game-area {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.score-display {
  position: absolute;
  top: 20px;
  right: 20px;
  background: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  font-size: 1rem;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
}

.phrase-container {
  background: none;
  padding: 40px 20px;
  margin: 40px 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-shadow: none;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.punctuation-options {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 20px 0;
  padding: 20px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #FFFEFA;
}

.punctuation-button {
  width: auto;
  height: 160px;
  border: none;
  border-radius: 16px;
  cursor: grab;
  transition: transform 0.2s;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  padding: 0 10px;
}

.character-image {
  width: 100%;
  height: auto;
  object-fit: contain;
  pointer-events: none;
  max-height: 160px;
}

.punctuation-button.dragging {
  cursor: grabbing;
  transform: scale(1.1);
  filter: drop-shadow(0 8px 16px rgba(0,0,0,0.2));
}

.punctuation-button:hover {
  transform: scale(1.05);
}

.back-button {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: #000000;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 12px;
  cursor: pointer;
  font-family: 'Rethink Sans', system-ui, sans-serif;
  font-weight: 800;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
}

.back-button:hover {
  opacity: 0.75;
  transition: ease-in-out 0.3s;
}

.drag-over {
  background-color: #f0f0f0;
  border-color: #666;
  transform: scale(1.02);
}

.drop-zone {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  border: 4px dashed #ccc;
  border-radius: 32px;
  transition: all 0.2s ease;
  vertical-align: top;
  margin: -10px 8px 8px 8px;
}

.drop-zone.drag-over {
  background-color: rgba(0, 0, 0, 0.05);
  border-color: #666;
  transform: scale(1.2);
}

.phrase-after {
  white-space: pre;
}